<template>
  <div
    :class="outerContainerClass"
    @mouseenter="this.mouseOver = true"
    @mouseleave="this.mouseOver = false"
    @click="onSelected"
    :style="backgroundColor"
  >
    <div class="outer-container box">
      <div class="inner-container">
        <div class="centered-content">
          <div class="ad-date roman-number">{{ ad }}</div>
          <div class="luna-date khmer-number">{{ lunaDay }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import consts from '../consts.js'
// const date = {
//   dayAd: 31,
//   dayLuna: 15,
//   isKert: true,
// };
import { EventAggregator } from "eventaggregator";

export default {
  name: "CalendarCell",
  props: {
    date: Object,
  },
  emits: ["selected"],
  mounted() {
    const ea = EventAggregator.getInstance();
    ea.on("cell-selected", () => {
      this.isSelected = false;
    });
    this.isSelected = this.date?.isSelected;
  },
  data() {
    return {
      mouseOver: false,
      isSelected: false,
    };
  },
  computed: {
    lunaDay() {
      return this.date?.day
        ? `${this.date.day}${this.date.kert ? "ក" : "រ"}`
        : null;
    },
    ad() {
      return this.date?.ad.day ?? "";
    },
    backgroundColor() {
      return this.mouseOver && this.date && this.date.ad.day
        ? "background:#4f8ffd"
        : "";
    },
    outerContainerClass() {
      return this.isSelected
        ? "outer-container selected box"
        : "outer-container box";
    },
  },
  methods: {
    onSelected() {
      if (!this.date || !this.date.ad.day) return;
      this.$emit("selected", this.date);
      this.isSelected = true;
    },
  },
  watch:{
    date(){
      this.isSelected = this.date?.isSelected;
    }
  }
};
</script>

<style scoped>
.ad-date {
  font-size: small;
}
.luna-date {
  font-size: x-small;
  color: black;
}

.selected {
  border-width: 2px !important;
  border-style: solid;
  border-color: red !important;
}
</style>
