const {
    MillisPerHour,
    MillisPerMinute,
    MillisPerDay,
} = require("./consts.js");
const {CeDate} = require("./CeDate.js")

const floor = Math.floor;


class Timestamp {
    #time;
    constructor(utc){
        if (utc === undefined) utc = Timestamp._getUtcNow();
        if (utc.constructor === Timestamp || utc.time) utc = utc.#time;
        this._setTimestamp(utc);
    }

    _setTimestamp(value) {this.#time = value; }

    get time () { return this.#time; }

    static _getUtcNow () { return Date.now ? Date.now() : +new Date(); }

    static UtcNow() { return new Timestamp(Timestamp._getUtcNow()); }
    
    static TimezoneOffset() { return new Date().getTimezoneOffset() * MillisPerMinute; }

    static LocalNow() { return new Timestamp(Timestamp._getUtcNow() - Timestamp.TimezoneOffset()); }
    
    static TimeInZone(zone) { return new Timestamp(Timestamp._getUtcNow() + zone*MillisPerHour); }

    static PhnomPenhTime() { return new Timestamp(Timestamp.TimeInZone(7)); }

    static PhnomPenhDate() { return floor(this.PhnomPenhTime().time / MillisPerDay)}
    
    
    toDateTime() { return new CeDate(floor(this.time/MillisPerHour)); }

}

module.exports = Timestamp;
