<template>
  <div class="khmer-text text-center">
    
    <div class="khmer-moul centered-content">
        <div style="height: 10px;"></div>
        <div>{{ yearBeText }}</div>
        <div class="khmer-text">{{ yearJsText }}</div>
        <div style="width: 300px; border-bottom: 1px solid #e3e3e3;" ></div>

        <div>{{ lunaMonths }}</div>    
        <div style="width: 300px; border-bottom: 1px solid #e3e3e3;" ></div>
    </div>

    <div id="date-text" class="khmer-text text-center" style="margin-top: 10px;">
      {{ lunaDateText }} 
      <br />
      {{ solarDateText }}
    </div>
    <div style="font-size: 11px; padding-bottom: 10px;">
        <span class="clickable" style="padding: 10px;" @click="gotoToday">ថ្ងៃនេះ</span>
        <span class="clickable" style="padding: 10px;" @click="onCopy">ចម្លង</span>
        <span v-if="runInOffice" class="clickable" style="margin: 10px;" @click="insertIntoDocument">បញ្ចូលក្នុងអត្ថបទ</span>
    </div>
    <div>
        <label for="input-month" class="khmer-text">ខែ: </label>
        <select v-model="monthAdIndex" class="khmer-text input-center" id="input-month">
        <option disabled value="">សូមរើសខែ</option>
        <option v-for="(item, index) in monthsAd" :value="index" :key="index"> {{ item }} </option>
        </select>
        <label for="input-year" class="khmer-text">ឆ្នាំ: </label>
        <input id="input-year" type="number" v-model.number="yearAd" class="input-center khmer-text" />
    </div>
    
    <div class="centered-content blue-background">
        
        <div class="wrapper">
          <CalendarCellHeader :title="item" v-for="(item, i) in weekdaysFull" :key="i" />
        </div>

        <div class="wrapper">
          <CalendarCell :date="d" v-for="(d, i) in cells" :key="i" @selected="onSelected" />
        </div>

      </div>
  </div>

</template>

<script>
import CalendarCell from "./CalendarCell.vue";
import CalendarCellHeader from "./CalendarCellHeader.vue";
import { EventAggregator } from "eventaggregator";
import { KDays, KCEMonths } from "../calendar/consts.js"
import { LunaDate, toStringKh, adToStringKh } from "../calendar/LunaDate.js";
import { CeCalendar } from "../calendar/CeCalendar.js";
import { toKh } from "@/calendar/utils";

function arrayRotate(arr, count) {
  const len = arr.length
  arr.push(...arr.splice(0, (-count % len + len) % len))
  return arr
}

function populateDatesOfMonth(monthAdIndex, yearAd) {
    const d1 = new LunaDate(yearAd, monthAdIndex + 1, 1);
    const ceCalendar = new CeCalendar();
    const daysPerMonth = ceCalendar.daysPerMonth(yearAd, monthAdIndex);
    const lunaDates = [...Array(daysPerMonth)].map((_, i) => d1.addDays(i).getDate())

    const today = new LunaDate().getDate();
    lunaDates.forEach(d => {
        d.isSelected = d.kean === today.kean;
    });
    if (lunaDates.filter(d => d.isSelected).length === 0) lunaDates[0].isSelected = true;
    return lunaDates;
}

const ea = EventAggregator.getInstance();
const monthsAd = KCEMonths;

const date = new LunaDate().getDate();
const monthAdIndex = date.ad.month - 1;
const yearAd = date.yearAd;
const lunaDates = populateDatesOfMonth(monthAdIndex, yearAd);
const startDate = (lunaDates[0].dayOfWeek + 6) % 7;
const today = new LunaDate().getDate();

export default {
  name: "CalendarFrame",
  components: { CalendarCell, CalendarCellHeader},
  emits: ["selected"],
  data() {
    return {
      // rows: 5, // 6 if 1st starts from 5th
      yearAd,
      monthAdIndex,
      columns: 7,
      weekdays: ["អា", "ច", "អ", "ពុ", "ព្រ", "សុ", "សៅ"],
      weekdaysFull: arrayRotate([...KDays], -1),
      monthsAd,
      dates: lunaDates,
      startDate,
      selecteDate: today,
    };
  },
  computed: {
    rows() {
      return (this.startDate ?? 0) + this.dates.length > 35 ? 6 : 5;
    },
    cellCount() {
      return this.rows * this.columns;
    },
    cells() {
      // var totalCells = this.dates.length + this.startDate;
      return [
        ...Array(this.startDate),
        ...this.dates,
        ...Array(this.cellCount - this.startDate - this.dates.length),
      ];
    },
    lunaDateText(){
        return toStringKh(this.selecteDate);
    },
    solarDateText(){
        return adToStringKh(this.selecteDate);
    },
    dateText() {
        return `${this.lunaDateText}\n${this.solarDateText}`;
    },
    lunaMonths(){
        const months = [...new Set(this.dates.map(d => d.month.name))];
        return months.join(" - ");
    },
    yearBeText(){
        const d0 = new LunaDate(this.yearAd, 1, 1).getDate();
        const d1 = new LunaDate(this.yearAd, 12, 31).getDate();
        const str = (d0.yearBe !== d1.yearBe)? `ពុទ្ធសករាជ ${d0.yearBe} - ${d1.yearBe}` : `ពុទ្ធសករាជ ${d0.yearBe}`;
        return toKh(str);
    },
    yearJsText(){
        const d0 = new LunaDate(this.yearAd, 1, 1).getDate();
        const d1 = new LunaDate(this.yearAd, 12, 31).getDate();
        const str = (d0.yearJs !== d1.yearJs)? `ច.ស.${d0.yearJs} - ${d1.yearJs}` : `ច.ស${d0.yearJs}`;
        return toKh(str);
    },
    runInOffice() { return window.Word || window.Excel; }
  },
  methods: {
    onSelected(date) {
      this.selecteDate = date;
      this.$emit("selected", date);
      ea.emit("cell-selected", date);
    },
    onCopy(){
        navigator.clipboard.writeText(this.dateText);
    },
    gotoToday(){
        const today = new LunaDate().getDate();
        if (this.monthAdIndex === today.ad.month - 1 && this.yearAd === today.yearAd){
            this.updateCalendar();
        }else{
            this.monthAdIndex = today.ad.month - 1;
            this.yearAd = today.yearAd;
        }
        
    },
    updateCalendar(){
        this.dates = populateDatesOfMonth(this.monthAdIndex, this.yearAd);
        this.startDate = (this.dates[0].dayOfWeek + 6) % 7;
    },
    async insertIntoDocument(){
        try {
            await window.Word.run(async (context) => {
                const range = context.document.getSelection();
                range.insertText(this.dateText, "replace");
            await context.sync();
            });
        } catch (error) {
            console.log(error);
        }
        
    }

  },
  watch: {
    monthAdIndex() {
        this.updateCalendar();
    },
    yearAd(){
        this.updateCalendar();
    },
    dates(){
        const date = this.dates.filter(d => d.isSelected);
        this.onSelected(date[0]);
    }
  },
};
</script>

<style scoped>
.wrapper {
    position:relative;
  display: grid;
  grid-gap: 0px;
  grid-template-columns: repeat(7, minmax(40px, 100px));
}

.box {
  background: #2a8dd4;
  color: #ecf2ff;
  border-width: 2px;
  border-style: solid;
  border-color: #2a8dd4;
  height: 38px;
  padding-top: 5px;
}

.input-center {
  text-align: center;
  width: 80px;
  margin-right: 20px;
  margin-bottom: 10px;
}

.blue-background{
    background: #2a8dd4;
}

.outer-container {
  display: table;
  overflow: hidden;
  cursor: pointer;
}

.inner-container {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.centered-content {
  display: inline-block;
  text-align: center;
}
</style>
