const {
    DaysPerYear,
    DaysPer400Years,
    DaysPer100Years,
    DaysPer4Years,
    DaysFrom1601To1970,
    GregorianEpochYear,
} = require("./consts.js");
const { floor, mod } = require("../funcs.js");

const OCT15_1582 = -141427;
const OCT04_1582 = -141438;

class CeCalendar {
    static MaxYear = 24500000000000;
    static MinYear = 1601;
    static MaxDays = 8948441249280837;
    static MinDays = -134774;

    static get instance() { return new this(); }

    calcYear(ts) { return this.daysToDate(ts).year; }
    calcMonth(ts) { return this.daysToDate(ts).month; }
    calcDay(ts) { return this.daysToDate(ts).day; }


    dateToKaen(year, month, day) {
        var days = this.dateToDays(year, month, day);
        if (days > OCT04_1582 && days < OCT15_1582) days += (OCT15_1582 - days);
        else if (days <= OCT04_1582) days += 10;
        return days;
    }
    dateToDays(year, month, day) {
        const calendar = this.#getCalendar(year, month, day);
        return calendar.dateToDays(year, month, day);
    }

    daysToDate(daysSinceEpoch) {
        const calendar = daysSinceEpoch < OCT15_1582 ? new JulianCalendar() : new GregorianCalendar();
        return calendar.daysToDate(daysSinceEpoch);
    }

    daysPerMonth(year, month){
        const calendar = this.#getCalendar(year, month);
        const daysToMonth = calendar.daysToMonth();
        return daysToMonth[month+1] - daysToMonth[month];
    }

    #getCalendar(year, month, day = 1){
        // eslint-disable-next-line no-unused-vars
        const estOCT4 = 577734;     // 365 * 1582 + 10*30 + 4 = 577734
        const estOCT15 = 577745;    // 365 * 1582 + 10*30 + 15 = 577745
        const estimated = year * 365 + month * 30 + day;
        const beforeOD = estimated < estOCT15;
        return beforeOD ? new JulianCalendar() : new GregorianCalendar();
    }
}


class JulianCalendar {

    daysToMonth366 = [0, 31, 60, 91, 121, 152, 182, 213, 244, 274, 305, 335, 366];
    daysToMonth365 = [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334, 365];
    static get instance() { return new GregorianCalendar(); }
    get name() { return "Julian Calendar"; }


    dateToDays(year, month, day) {
        const years = year - GregorianEpochYear;
        const count4years = Math.floor((year - 1) / 4) - 492 + 3;
        const months = this.daysToMonth(year);
        return count4years + years * DaysPerYear + months[month - 1] + day - 1;
    }

    daysToDate(daysSinceEpoch) {
        const daysSinceJan1st =
            [
                [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334, 365], // 365 days, non-leap
                [0, 31, 60, 91, 121, 152, 182, 213, 244, 274, 305, 335, 366]  // 366 days, leap
            ];

        var days = daysSinceEpoch + 719174 //719542;

        const wday = mod(days + 6, 7); // day of week

        var quadrennials = floor(days / DaysPer4Years); // 4*(365+1/4) //*24*3600
        days -= quadrennials * DaysPer4Years;

        var annuals = floor(days / DaysPerYear); // 365*24*3600
        if (annuals > 3) {
            annuals = 3;
        }
        days -= annuals * DaysPerYear;

        // Calculate the year and find out if it's leap
        const epochYear = 1;
        const year = epochYear + quadrennials * 4 + annuals;
        const leap = +((!(year % 4)) > 0);

        // Calculate the day of the year and the time
        var yday = days;

        // Calculate the month
        var month, day = 1;
        const daysToMonth = daysSinceJan1st[leap];
        for (month = 1; month < 13; month++) {
            if (yday < daysToMonth[month]) {
                day += yday - daysToMonth[month - 1];
                break;
            }
        }

        return {
            year,
            month,   // [1,12]  (month)
            day,     // [1,31]  (day of month)
            wday,    // [0,6]   (day since Sunday AKA day of week)
            yday,    // [0,365] (day since January 1st AKA day of year)
        };
    }

    daysToMonth(year) { return this.isLeapYear(year) ? this.daysToMonth366 : this.daysToMonth365; }

    isLeapYear(year) {
        return year % 4 === 0;
    }

}

class GregorianCalendar {

    daysToMonth366 = [0, 31, 60, 91, 121, 152, 182, 213, 244, 274, 305, 335, 366];
    daysToMonth365 = [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334, 365];
    static get instance() { return new GregorianCalendar(); }
    get name() { return "Gregorian Calendar"; }

    dateToKaen(year, month, day) { return this.dateToDays(year, month, day); }
    dateToDays(year, month, day) {
        const years = year - GregorianEpochYear;

        const count4years = floor((year - 1) / 4) - 492;
        const count100years = floor((year - 1) / 100) - 19;
        const count400years = floor((year - 1) / 400) - 4;

        const months = this.daysToMonth(year);
        var days = years * DaysPerYear + months[month - 1] + day - 1;
        days += (count4years - count100years + count400years);
        return days;
    }
    // daysToDate(daysSinceEpoch) { return this.daysToDate(daysSinceEpoch); }
    daysToDate(daysSinceEpoch) {
        const daysSinceJan1st =
            [
                [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334, 365], // 365 days, non-leap
                [0, 31, 60, 91, 121, 152, 182, 213, 244, 274, 305, 335, 366]  // 366 days, leap
            ];


        // Re-bias from 1970 to 1601:
        // 1970 - 1601 = 369 = 3*100 + 17*4 + 1 years (incl. 89 leap days) =
        // (3*100*(365+24/100) + 17*4*(365+1/4) + 1*365)*24*3600 seconds = 11644473600 seconds
        var days = daysSinceEpoch + DaysFrom1601To1970;

        const wday = mod(days + 1, 7); // day of week

        // Remove multiples of 400 years (incl. 97 leap days)
        var quadricentennials = floor(days / DaysPer400Years); // 400*365.2425 //*24*3600
        days %= DaysPer400Years;

        // Remove multiples of 100 years (incl. 24 leap days), can't be more than 3
        // (because multiples of 4*100=400 years (incl. leap days) have been removed)
        var centennials = floor(days / DaysPer100Years); // 100*(365+24/100) //*24*3600
        if (centennials > 3) {
            centennials = 3;
        }
        days -= centennials * DaysPer100Years;

        // Remove multiples of 4 years (incl. 1 leap day), can't be more than 24
        // (because multiples of 25*4=100 years (incl. leap days) have been removed)
        var quadrennials = floor(days / DaysPer4Years); // 4*(365+1/4) //*24*3600
        if (quadrennials > 24) {
            quadrennials = 24;
        }
        days -= quadrennials * DaysPer4Years;

        // Remove multiples of years (incl. 0 leap days), can't be more than 3
        // (because multiples of 4 years (incl. leap days) have been removed)
        var annuals = floor(days / DaysPerYear); // 365*24*3600
        if (annuals > 3) {
            annuals = 3;
        }
        days -= annuals * DaysPerYear;

        // Calculate the year and find out if it's leap
        const epochYear = 1;
        const year = epochYear + quadricentennials * 400 + centennials * 100 + quadrennials * 4 + annuals;
        const leap = +((!(year % 4) && ((year % 100) || !(year % 400))) > 0);

        // Calculate the day of the year and the time
        var yday = days;

        // Calculate the month
        var month, day = 1;
        const daysToMonth = daysSinceJan1st[leap];
        for (month = 1; month < 13; month++) {
            if (yday < daysToMonth[month]) {
                day += yday - daysToMonth[month - 1];
                break;
            }
        }

        return {
            year,
            month,   // [1,12]  (month)
            day,     // [1,31]  (day of month)
            wday,    // [0,6]   (day since Sunday AKA day of week)
            yday,    // [0,365] (day since January 1st AKA day of year)
        };
    }
    secondsToDate(ts) {
        // https://stackoverflow.com/questions/11188621/how-can-i-convert-seconds-since-the-epoch-to-hours-minutes-seconds-in-java/11197532#11197532
        var SecondsSinceEpoch = ts;
        var sec;
        var quadricentennials, centennials, quadrennials, annuals/*1-ennial?*/;
        var year, leap;
        var yday, hour, min;
        var month, mday, wday;
        const daysSinceJan1st =
            [
                [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334, 365], // 365 days, non-leap
                [0, 31, 60, 91, 121, 152, 182, 213, 244, 274, 305, 335, 366]  // 366 days, leap
            ];
        /*
        400 years:
        
        1st hundred, starting immediately after a leap year that's a multiple of 400:
        n n n l  \
        n n n l   } 24 times
        ...      /
        n n n l /
        n n n n
        
        2nd hundred:
        n n n l  \
        n n n l   } 24 times
        ...      /
        n n n l /
        n n n n
        
        3rd hundred:
        n n n l  \
        n n n l   } 24 times
        ...      /
        n n n l /
        n n n n
        
        4th hundred:
        n n n l  \
        n n n l   } 24 times
        ...      /
        n n n l /
        n n n L <- 97'th leap year every 400 years
        */
        const SecondsPer400Y = 12622780800; // 400*365.2425*24*3600
        const SecondsPerDay = 86400;
        // Re-bias from 1970 to 1601:
        // 1970 - 1601 = 369 = 3*100 + 17*4 + 1 years (incl. 89 leap days) =
        // (3*100*(365+24/100) + 17*4*(365+1/4) + 1*365)*24*3600 seconds = 11644473600 seconds
        sec = SecondsSinceEpoch + 11644473600;

        wday = (floor(sec / SecondsPerDay + 1) % 7); // day of week

        // Remove multiples of 400 years (incl. 97 leap days)
        quadricentennials = floor(sec / SecondsPer400Y); // 400*365.2425*24*3600
        sec %= SecondsPer400Y;

        // Remove multiples of 100 years (incl. 24 leap days), can't be more than 3
        // (because multiples of 4*100=400 years (incl. leap days) have been removed)
        centennials = floor(sec / 3155673600); // 100*(365+24/100)*24*3600
        if (centennials > 3) {
            centennials = 3;
        }
        sec -= centennials * 3155673600;

        // Remove multiples of 4 years (incl. 1 leap day), can't be more than 24
        // (because multiples of 25*4=100 years (incl. leap days) have been removed)
        quadrennials = floor(sec / 126230400); // 4*(365+1/4)*24*3600
        if (quadrennials > 24) {
            quadrennials = 24;
        }
        sec -= quadrennials * 126230400;

        // Remove multiples of years (incl. 0 leap days), can't be more than 3
        // (because multiples of 4 years (incl. leap days) have been removed)
        annuals = floor(sec / 31536000); // 365*24*3600
        if (annuals > 3) {
            annuals = 3;
        }
        sec -= annuals * 31536000;

        // Calculate the year and find out if it's leap
        year = 1601 + quadricentennials * 400 + centennials * 100 + quadrennials * 4 + annuals;
        leap = +(!(year % 4) && ((year % 100) || !(year % 400)));

        // Calculate the day of the year and the time
        yday = floor(sec / 86400);
        sec %= 86400;
        hour = floor(sec / 3600);
        sec %= 3600;
        min = floor(sec / 60);
        sec %= 60;

        // Calculate the month
        mday = 1;
        for (month = 1; month < 13; month++) {
            if (yday < daysSinceJan1st[leap][month]) {
                mday += yday - daysSinceJan1st[leap][month - 1];
                break;
            }
        }

        // Fill in C's "struct tm"
        const ptm = {
            second: sec,          // [0,59]
            minute: min,          // [0,59]
            hour: hour,        // [0,23]
            day: mday,        // [1,31]  (day of month)
            month: month, // - 1,    // [0,11]  (month)
            year: year, //- 1900, // 70+     (year since 1900)
            wday: wday,        // [0,6]   (day since Sunday AKA day of week)
            yday: yday,        // [0,365] (day since January 1st AKA day of year)
        };
        return ptm;
    }


    daysToMonth(year) { return this.isLeapYear(year) ? this.daysToMonth366 : this.daysToMonth365; }

    isLeapYear(year) {
        return year % 4 === 0 && (year % 100 !== 0 || year % 400 === 0);
    }

}

module.exports.CeCalendar = CeCalendar;
module.exports.Gregorian = GregorianCalendar;
module.exports.Julian = JulianCalendar;

