const { MillisPerDay } = require("./consts.js");
const { CeCalendar } = require("./CeCalendar.js");
const Timestamp = require("./timestamp.js");
const { floor } = require("../funcs.js");

class DateArgs {

    /**
     * 
     * @param {Number} year year
     * @param {Number} month month
     * @param {Number} day day
     */
    constructor(year, month, day, empty = undefined){
        this.year = year;
        this.month = month,
        this.day = day,
        this.invalid = empty;
    }
    year;
    month;
    day;
    invalid;
    get isEmpty () { return this.invalid; }
    static get Empty () { return new DateArgs(undefined, undefined, undefined, true); }
}

const OCT15_1582 = -141427;
const OCT04_1582 = -141438;

class CeDate {
    #days;
    #calendar;
    constructor(value = DateArgs.Empty, calendar = new CeCalendar()) { // CeCalendar.instance
        this.#calendar = calendar;
        
        if (value === undefined || value.isEmpty) value = floor(Timestamp.PhnomPenhTime().time/MillisPerDay);
        // if (value.constructor === Number) value = value;
        if (value.constructor === Timestamp || value.time) value = floor(value.time/MillisPerDay);
        if (value.year !== undefined && value.month && value.day) {
            const year = value.year;
            const month = value.month;
            const day = value.day;
            value = this.#calendar.dateToDays(year, month, day);
        }
        this.#setTimestamp(value);
    }
    #setTimestamp(days) {this.#days = days; }
    static fromKaen(kaen) {
        var days = kaen; 
        if (days < OCT15_1582) days -= 10;
        if (days < OCT15_1582 && days > OCT04_1582){ days += (OCT15_1582 - days) }
        return new CeDate(days);
    }

    get days () { return this.#days; }
    get year () { return this.get().year; }
    get month () { return this.get().month; }
    get day () { return this.get().day; }
    get calendar (){ return this.#calendar; }
    get() {  return this.#calendar.daysToDate(this.days);  }

    static Now() { return new this();}
    static PPNow() { return new this();}
}

module.exports.CeDate = CeDate;