const { EDays, KDays, LunaMonths, EAnimalYears, AnimalYears, ELunaMonths: EMonths, Saks, KCEMonths } = require("./consts.js");
const { MillisPerDay } = require("./consts.js");
const { CeDate } = require("./CeDate.js");
const { CeCalendar } = require("./CeCalendar.js");
const { YearType } = require("./year-type.js");
const Timestamp = require("./timestamp.js");
const { fdivide: divide, mod } = require("../funcs.js");
const { toKh } = require("./utils.js");

const floor = Math.floor;





class LunaDate {
    static EPOCH_OFFSET_AHAKUN = 486526; // 486526+1;
    static EPOCH_OFFSET_FROM_UTC = -105;
    static MILLIS_PER_DAY = 8.64e7;
    static OFFSET_SAK = 8;
    static OFFSET_ANIMAL = 9;
    static OFFSET_WEEKDAY = 0;

    static DaysBeforeJs = 431370; //    431743 - 374 +2;
    static DaysTo1970 = 486421;//  EPOCH_OFFSET_AHAKUN + EPOCH_OFFSET_FROM_UTC (486526-105);

    #kaen;
    #ceCalendar = new CeCalendar(); // CeCalendar.instance;

    constructor(...args) {
        if (args === undefined || args === false || args[0] === undefined) args = this.daysToKaen(floor(Timestamp.PhnomPenhTime().time / MillisPerDay));
        if (args.constructor === Date) args = floor(args.getTime() / MillisPerDay);
        if (args.constructor === Array && args.length === 1) {
            const a = args[0];
            if ((a.year !== undefined && a.year.constructor === Number) && a.month && a.day) args = this.dateToKaen(a.year, a.month, a.day);

            // days from 1970 Unix Epoch
            else args = this.daysToKaen(a);

        }
        if (args.constructor === Array && args.length === 3) args = this.dateToKaen(args[0], args[1], args[2]);
        this.#kaen = args;
    }

    get kaen() { return this.#kaen; }
    static fromKaen = (kaen) => new LunaDate(kaen - LunaDate.DaysTo1970 - LunaDate.DaysBeforeJs);
    addDays = (days) => new LunaDate(this.daysFrom1970() + days);

    dateToKaen = (year, month, day) => this.daysToKaen(this.#ceCalendar.dateToKaen(year, month, day));
    daysToKaen = (sDays) => sDays + LunaDate.DaysTo1970 + LunaDate.DaysBeforeJs;
    daysFromJs = () => this.kaen - LunaDate.DaysBeforeJs;
    daysFrom1970 = () => this.kaen - LunaDate.DaysBeforeJs - LunaDate.DaysTo1970;
    elapsedDays = () => this.daysFromJs();// + LunaDate.EPOCH_OFFSET_AHAKUN;
    getYearJs = () => {
        const ed = this.elapsedDays();
        return floor(((ed * 800) - 1173) / 292207);
    }
    daysFrom1970 = () => this.elapsedDays() - LunaDate.DaysTo1970;
    elapsedDaysToUtc = (elapsedDays) => (elapsedDays - LunaDate.EPOCH_OFFSET_AHAKUN - LunaDate.EPOCH_OFFSET_FROM_UTC) * LunaDate.MILLIS_PER_DAY;
    adDaysFromDayZero() {
        // https://stackoverflow.com/questions/8619879/javascript-calculate-the-day-of-the-year-1-366
        const date = new Date(this.kaen * MillisPerDay);

        const start = new Date(date.getFullYear(), 0, 0);
        const offset = ((start.getTimezoneOffset() - date.getTimezoneOffset()) * 60 * 1000);

        return floor((date - start + offset) / LunaDate.MILLIS_PER_DAY);
    }

    calcYearProperty(jsYear) {
        function propCalc(yearJs) {
            const { result, remainder: ahakun_mod } = divide((yearJs * 292207) + 1173, 800);// divide((yearJs*292207) + 373, 800);
            const ahakun = result //+ 1;
            const kromthupul = 800 - ahakun_mod;
            const oicheapul = mod((ahakun + 2611), 3232);
            const { result: leapTithis, remainder: avarman } = divide(floor(ahakun * 11 + 650), 692);
            const elapsedTithis = ahakun + leapTithis;
            const { result: totalMonths, remainder: bodethey } = divide(elapsedTithis, 30);
            const isExtraDay = (avarman < 137 && kromthupul > 207) || (avarman < 126 && kromthupul < 208);
            const isExtraMonth = bodethey < 6 || bodethey > 25;
            return { yearJs, ahakun, kromthupul, avarman, bodethey, totalMonths, oicheapul, isExtraDay, isExtraMonth, elapsedTithis };
        }
        function hasExtraMonth(prop, nextProp) {
            const case25 = prop.bodethey === 25 && nextProp.bodethey !== 5;
            const case26 = prop.bodethey === 24 && nextProp.bodethey === 6;
            return (prop.isExtraMonth || case25 || case26);
        }

        function hasExtraDay(prop, preProp, nextProp) {
            const preIsExtraDay = preProp.isExtraDay || (preProp.bodethey === 25 && prop.bodethey === 5);
            const preIsExtraMonth = hasExtraMonth(preProp, prop);
            const previousYearHasGreatLeap = preIsExtraDay && preIsExtraMonth;
            return previousYearHasGreatLeap || prop.isExtraDay || (prop.bodethey === 25 && nextProp.bodethey === 5);
        }

        const prop = propCalc(jsYear);
        const propNextYear = propCalc(jsYear + 1);
        const propPrevYear = propCalc(jsYear - 1);

        const isExtraMonth = hasExtraMonth(prop, propNextYear);
        const isExtraDay = hasExtraDay(prop, propPrevYear, propNextYear);
        const yearType = (isExtraMonth * YearType.ExtraMonth) + (isExtraDay * YearType.ExtraDay);

        const dayCount = isExtraMonth ? 384 : isExtraDay ? 355 : 354;
        const monthCount = isExtraMonth ? 13 : 12;
        const months = isExtraMonth ? LunaMonths.ExtraMonth : isExtraDay ? LunaMonths.ExtraDay : LunaMonths.Normal;

        const lerngSak = this.calcLerngSak({ ...prop, months }, propPrevYear);
        const adLeap = prop.kromthupul <= 207;

        const nextYear = () => this.calcYearProperty(jsYear + 1);
        const previousYear = () => this.calcYearProperty(jsYear - 1);

        return { ...prop, adLeap, isExtraDay, isExtraMonth, dayCount, monthCount, months, yearType, lerngSak, nextYear, previousYear };
    }

    beToJsYear = (days, be) => days >= 162 ? be - 1182 : be - 1181;
    jsToBeYear(days, jsYear) {
        // new BE year starts from 1Roch of Visak
        const pisak1Roch = 162;

        // this system is based on the beggining of JS year.
        // and (in 2022) JS year is 27days earlier than AD
        if (days > pisak1Roch) return jsYear + 1182;
        else return jsYear + 1181;
    }
    calcDayMonth(days, yearProperty) {
        let d = days; let m = 0;
        if (d > yearProperty.dayCount) d -= yearProperty.dayCount;

        const months = yearProperty.months;
        for (m = 0; m < months.length; m++) {
            const month = months[m];
            if (d <= month.days) break;
            d -= month.days;
        }
        return { days: d, month: months[m], monthIndex: m };
    }
    calcLerngSak(prop, propPreviousYear) {

        const dayIndex = mod(prop.ahakun, 7);
        const dayNameKh = KDays[dayIndex];
        const dayNameEn = EDays[dayIndex];
        const elapsedDaysJs = prop.ahakun;
        const elapsedDaysUtc = elapsedDaysJs - LunaDate.DaysTo1970; //this.elapsedDaysToUtc(elapsedDaysJs);
        var monthIndex = 4; // LMChet
        var day = prop.bodethey;
        var kert = true;
        if (prop.bodethey > 15) { kert = false; }
        if (prop.bodethey <= 5) { monthIndex = 5; }  // LMPisak 

        const caseUon = prop.bodethey <= 5 || (propPreviousYear.isExtraDay && propPreviousYear.isExtraMonth);
        if (caseUon) { day += 1; }
        const toLunaDate = () => { return new LunaDate(elapsedDaysUtc); }
        return { day, kert, month: prop.months[monthIndex], dayNameEn, dayNameKh, dayIndex, elapsedDaysUtc, elapsedDaysJs, toLunaDate };
    }
    getAnimalAndSak(jsYear, days, lerngSak) {
        let indexSak = mod((jsYear + LunaDate.OFFSET_SAK), 10);
        let indexAnimal = mod((jsYear + LunaDate.OFFSET_ANIMAL), 12);
        const begginOfChet = lerngSak.month.startDay + lerngSak.day;
        if (days > begginOfChet) {
            indexAnimal = mod((indexAnimal + 1), 12);
            indexSak = mod((indexSak + 1), 10);
        }
        return { indexAnimal, indexSak };
    }
    getDate = () => {
        var jsYear = this.getYearJs();
        var prop = this.calcYearProperty(jsYear);
        // const propPreviousYear = this.calcYearProperty(jsYear - 1);
        const daysJs = this.kaen - LunaDate.DaysBeforeJs;


        const lerngSak = prop.lerngSak; //this.calcLerngSak(prop, propPreviousYear);
        const d1KertOfLsMonth = lerngSak.month.startDay + lerngSak.day;
        var days = daysJs - lerngSak.elapsedDaysJs + d1KertOfLsMonth;

        const dayMonth = this.calcDayMonth(days, prop);
        const kert = dayMonth.days <= 15;
        const day = dayMonth.days;//  kert? dayMonth.days : dayMonth.days -15;
        const month = dayMonth.month;
        const yearBe = this.jsToBeYear(days, jsYear);
        const animalAndSak = this.getAnimalAndSak(jsYear, days, lerngSak);
        const dayOfWeek = mod(daysJs, 7); // daysJs - 7 * Math.floor(daysJs/7); 
        // console.log('this.daysFrom1970()', this.daysFrom1970(), {prop});

        var ad = CeDate.fromKaen(this.daysFrom1970()).get();
        const yearAd = ad.year;
        const monthAd = ad.month;
        const dayAd = ad.day;

        return { ydays: days, ad, kean: this.kaen, yearAd, monthAd, dayAd, dayOfWeek, day, month, yearBe, kert, yearJs: jsYear, animal: animalAndSak.indexAnimal, sak: animalAndSak.indexSak, yearType: prop.yearType };
    };
}


LunaDate.prototype.toString = function () {
    const d = this.getDate();
    const M = d.yearType >= YearType.ExtraMonth ? EMonths.ExtraMonth : EMonths.Normal;
    // console.log(YearType.ExtraMonth && YearType.ExtraMonth);
    return `${EDays[d.dayOfWeek]} ${(d.kert ? d.day + "K" : d.day - 15 + "R")} Month.${M[d.month.index]} Year.${EAnimalYears[d.animal]} BE.${d.yearBe}}`;
}
function toStringKh(d) {
    const str = `ថ្ងៃ${KDays[d.dayOfWeek]} ${(d.kert ? d.day + "កើត" : d.day - 15 + "រោច")} ខែ${d.month.name} ឆ្នាំ${AnimalYears[d.animal]} ${Saks[d.sak]} ព.ស.${d.yearBe}`;
    return toKh(str);
}
function adToStringKh(d = new LunaDate().getDate()) {
    const str = `ថ្ងៃទី${d.ad.day} ខែ${KCEMonths[d.ad.month-1]} ឆ្នាំ${d.ad.year}`;
    return toKh(str)
}
module.exports.LunaDate = LunaDate;
module.exports.toStringKh = toStringKh;
module.exports.adToStringKh = adToStringKh;

