
function toKh(nums, backward=false) {
    const kh = "០១២៣៤៥៦៧៨៩";
    const en = "0123456789";
    let result = `${nums}`;
    for (let i = 0; i < kh.length; i++) {
        result = backward? result.replaceAll(kh[i], en[i]): result.replaceAll(en[i], kh[i]);
    }
    return result;
}

const toEn  = (value) => toKh(value, true);
const isNaN = (value) => value !== value;
const extractNumbers = (string) => string.match(/\d+/g);
const utcToday = function() {
    const d = new Date();
    const day = d.getDate();
    const month = d.getMonth();
    const year = d.getFullYear();
    return Date.UTC(year, month, day);
};

module.exports.isNaN = isNaN;
module.exports.isNumber = (obj) => !isNaN(Number(obj)) || (Boolean(obj) && obj.constructor === Number);
module.exports.isString = (obj) => Boolean(obj) && obj.constructor === String;
module.exports.isNullOrEmpty = (obj) => obj === null || obj === undefined || obj === "";
module.exports.toKh = toKh;
module.exports.toEn = toEn;
module.exports.extractNumbers = extractNumbers;
module.exports.utcToday = utcToday;
